.Card {
  /* border: 1px solid #9fe262; */
  background-color: white;
  position: fixed;
  width: 180px;
  margin: 20px;
  border-radius: 5px;
  overflow-x: hidden;
  min-height: 350px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12) !important;
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12) !important;
}

.arrows {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
  position: absolute;
  bottom: 45px;
  left: 25px;
}

.arrow-icon {
  opacity: 0.4;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  padding: 5px 10px;
}

.arrow-icon:hover {
  opacity: 1;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .Card {
    position: relative;
    -ms-flex-item-align: center;
        align-self: center;
  }
}

.label {
  margin: 5px;
  font-style: bold;
  font-size: 10px;
  color: #159782;
}

.string {
  background-color: #9eff41;
  color: #159782;
  font-size: 36px;
  font-weight: 800;
  padding: 20px;
}

.definition,
.pinyin {
  color: #00c3a4;
  padding: 0 20px 20px 20px;
  opacity: 0;
  -webkit-transition: opacity 2s;
  -o-transition: opacity 2s;
  transition: opacity 2s;
}

.definition:hover,
.pinyin:hover {
  opacity: 1;
  cursor: pointer;
}

.footer:hover {
  cursor: pointer;
  color: white;
}

.footer {
  -webkit-transition: color 0.4s;
  -o-transition: color 0.4s;
  transition: color 0.4s;
  position: absolute;
  bottom: 0;
  height: 40px;
  width: 100%;
  background-color: #50e5cd;
  color: #c1fff5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}



