td {
  text-align: left;
  padding-right: 25px;
}

a.fetch {
  color: grey;
}

a.fetch:hover {
  color: black;
  cursor: pointer;
}

.row:hover {
  cursor: pointer;
}

.row {
  width: 100%;
  height: 25px;
}

.add {
  font-weight: 800;
  font-size: 24px;
  opacity: 0.4;
  color: #159782;
  -webkit-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.add:hover {
  opacity: 0.8;
}

.search {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  top: 105px;
  left: 250px;
  z-index: 3;
  width: 100%;
  padding-top: 50px;
  background-color: #f2f2f2;
}

.container {
  margin-left: 250px;
  padding-top: 130px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}

input {
  margin-left: 10px;
}

@media only screen and (max-width: 600px) {
  .container {
    margin-left: 25px;
    padding-top: 0;
  }

  .search {
    position: relative;
    padding-top: 25px;
    top: 0;
    left: 0;
  }
}



